import refresh from "../refresh.vue";
import content from '../xyz-masonry/xyz-masonry.vue';
import MescrollUni from "../mescroll-uni/mescroll-uni.vue"; 
export default {
	components: {
		refresh,
		content,
		MescrollUni
	},
	data() {
		return {
			ismH:9999,
			currentTab: 0,
			tabClick: 0,
			navIndex:'',
			dataList:[],
			isPage:1,
			addPage:1,
			iscid:1,
			jobArr:'',
			jobId:'',
			cIndex:1,
			inputContent:'',
			cid: null
		}
	},
	watch: {
		masonryList(newVal, oldVal) {
			this.handleData(newVal);
		}
	},
	computed:{
		isModel:function(){
			return this.$store.state.model;
		},
		isHeight:function(){
			return this.$store.state.windowHeight-51
		},
		isBgH:function(){
			return this.$store.state.windowHeight-220
		},
		contentH:function(){
			return this.$store.state.windowHeight-220
		}
	},
	onLoad() {
		this.handleData(this.masonryList);
		
	},
	onReady() {
		//console.log(this.$store.state.model)
		var cid = JSON.stringify(this.cIndex)
		this.$store.commit('changeCid',cid)
		this.initData()
	},
	methods: {
		refreshStart(e) {
			this.$refs.refresh.refreshStart(e);
					// this.navClick(this.tabClick)
		},
		refreshMove(e){
			this.$refs.refresh.refreshMove(e);
		
		},
		refreshEnd(e) {
			this.$refs.refresh.refreshEnd(e);
		},
	
		isRequest(){
			return new Promise(function( reslove,reject ) {
					reslove()
			    })
		
		},
		refresh(){
			var _this = this
			   let openid=uni.getStorageSync('openid');
			   let page = _this.isPage
			   let cid = _this.cIndex
			   let addPage = _this.addPage
			   addPage = 1
			   _this.addPage = addPage
			   _this.$http.getList({
				   openId:openid,
				   page: page,
				   cid: cid
			   }).then(res => {
				   let list=res.data.data.list;
				   if(list!=undefined){
				   	_this.dataList=list;
				   }
			   })
		},
		// 导航栏点击
		navClick(index) {
			this.currentTab = index	//设置swiper的第几页
			this.tabClick = index	//设置导航点击了哪一个
			this.navIndex=index
			let openid=uni.getStorageSync('openid');
			let _this=this;
			let cIndex=this.jobId[index];
			_this.$store.commit('changeCid',cIndex)
			_this.cIndex=cIndex;
			_this.$http.getList({
				openId:openid,
				page:_this.isPage,
				cid:cIndex
			}).then(res => {
				let list=res.data.data.list;
				if(list!=undefined){
					_this.dataList=list;
				}
			})
			
		},
		// swiper 滑动
		swiperTab: function(e) {
			//console.log(e)
	
			var index = e.detail.current
			let cIndex=this.jobId[index];
			this.cIndex=cIndex;
			 this.currentTab = index	//设置swiper的第几页
			 this.tabClick = index	//设置导航点击了哪一个
			 this.navIndex=index//获取索引
			this.navClick(index)
		},
		lower1(){
			let openid=uni.getStorageSync('openid');
			// console.log(openid)
			let _this=this;
			_this.addPage+=1;
			_this.$http.getList({
				openId:openid,
				page:_this.addPage,
				cid:_this.cIndex
			}).then(res => {
				let list=res.data.data.list;
				console.log(list)
				if(list!=undefined){
					for(let i in list){
						_this.dataList.push(list[i]);
					}
				}
			})
			
		},
		initData:function(){
			let openid=uni.getStorageSync('openid');
			// console.log(openid)
			let _this=this;
			_this.$http.getWork({
				openId:openid
			}).then(res => {
				let arr1=[];
				let arr2=[];
				let dataArr=res.data.data;
				for(let i in dataArr){
					arr1.push(dataArr[i].cname);
					arr2.push(dataArr[i].cid)
				}
				_this.jobArr=arr1;
				_this.jobId=arr2;
			})
			_this.$http.getList({
				openId:openid,
				page:_this.isPage,
				cid:1
			}).then(res => {
				let list=res.data.data.list;
				//console.log(list)
				if(list!=undefined){
					for (let i in list) {
						_this.dataList.push(list[i]);
					}
				}
			})
		},
		getContent:function(e){
			//console.log(e.detail.value)
			this.inputContent=e.detail.value
		},
		toSearch:function(){
			let _this=this;
			uni.navigateTo({
				url:'/pages/search/index?content='+_this.inputContent
			})
		}
	}
}
